<template>
  <div class="">
    <div class="panel panel-default" v-loading.body="loading" v-if="user.name !== ''">
      <div class="panel-heading">
        <h6 class="panel-title">Editar - {{ user.name }}<a class="heading-elements-toggle"><i class="icon-more"></i></a></h6>
      </div>
      <div class="panel-body">
        <form @keydown="form.errors.clear($event.target.name)">
          <alert-errors :form="form" classe="alert bg-danger alert-styled-left" message=""></alert-errors>
          <div class="form-group">
            <label>Name:</label>
            <input type="text" class="form-control" v-model="form.name">
          </div>
          <div class="form-group">
            <label>Email:</label>
            <input type="text" class="form-control" v-model="form.email">
          </div>
          <div class="form-group">
            <label>Password:</label>
            <input type="password" class="form-control" v-model="form.password">
          </div>
          <div class="form-group">
            <label>Password Confirmation:</label>
            <input type="password" class="form-control" v-model="form.password_confirmation">
          </div>
          <div class="form-group">
            <label for="">Clique para {{ form.active === true ? 'Desativar' : 'Ativar' }}</label>
            <br>
            <el-tooltip :content="form.active === true ? 'Usuário Ativo' : 'Usuário Inativo'" placement="top">
              <el-switch
              v-model="form.active"
              on-color="#13ce66"
              off-color="#ff4949"
              on-text="Ativo"
              off-text="">
            </el-switch>
            </el-tooltip>
          </div>
          <div class="form-group">
            <label for="">Níveis de Permissão</label><br>
            <ul>
              <li v-for="row in user.roles">{{row.display_name}}</li>
            </ul>
            <el-select v-model="form.roles" multiple placeholder="Escolha os Niveis de Permissão">
              <el-option
              v-for="item in roles"
              :key="item.id"
              :label="item.display_name"
              :value="item.id">
            </el-option>
          </el-select>
        </div>
        <div class="form-group">
          <button type="button" @click="updateUser" class="btn btn-primary" name="button">Atualizar</button>
        </div>
        </form>
      </div>
    </div>

    <!-- Nenhum usuário encontrado -->
    <div class="" v-else>
      <div class="alert bg-danger">
        <p>Nenhum usuário encontrado!</p>
      </div>
    </div>
  </div>
</template>

<script>
import { userURL, roleUrl } from './../../../services/config'
import Form from 'vform'
import { http } from 'plugins/http'
export default {
  metaInfo: {
    titleTemplate: '%s - Editar Usuário'
  },
  data() {
    return {
      loading: false,
      roles: [],
      user: '',
      form: new Form({
        name: '',
        email: '',
        password: '',
        password_confirmation: '',
        roles: [],
        active: true
      })
    }
  },
  methods: {
    fetch() {
      this.loading = true
      http.get(userURL + '/' + this.$route.params.id).then(response => {
        console.log(response)
        this.user = response.data
        this.form.name = response.data.name
        this.form.email = response.data.email
        this.form.active = response.data.deleted_at === null
        this.loading = false
      }).catch(response => {
        console.log(response)
        this.loading = false
        this.form.reset()
      })
    },
    updateUser() {
      this.form.put(userURL + '/' + this.$route.params.id).then(response => {
        swal(`Usuário ${this.form.name} atualizado com sucesso!`, "", "success")
        this.$router.push({ name: 'user.index' })
      }).catch(error => {
        console.log(error)
        swal("Error", "", "error")
      })
    }
  },
  created() {
    this.fetch()
    http.get(roleUrl).then(response => {
      this.roles = response.data
    })
  },
  watch: {
    '$route': 'fetch'
  }
}
</script>

<style lang="css">
</style>



